<template>
  <div>
    <el-dialog :title="$t('order.ordertrace')" :visible.sync="traceVisible"  width="760px">
      <el-table :data="traceList" :header-cell-style="{background:'#DFEBF9',color:'#606266'}" max-height="300"
        :empty-text="$t('common.nodata')" :row-class-name="tableRowClassName" border>
        <el-table-column :show-overflow-tooltip='true' prop="master_pn" :label="$t('order.master_pn')">
        </el-table-column>
        <el-table-column :show-overflow-tooltip='true' prop="mate_state" :label="$t('order.mate_state')" :formatter="stateFormatter">
        </el-table-column>
        <el-table-column :show-overflow-tooltip='true' prop="need_num" :label="$t('order.need_num_count')">
        </el-table-column>
        <el-table-column :show-overflow-tooltip='true' prop="send_num" :label="$t('order.send_num')">
        </el-table-column>
        <el-table-column :label="$t('common.operation')" width="120px">
          <template slot-scope="scope">
             <el-button
             :disabled="!scope.row.mate_state"
              @click.native.prevent="getDetail(scope.row.send_mate_list)"
              type="text"
              size="small">
              {{$t('order.detail')}}
            </el-button>
          </template>
        </el-table-column>
      </el-table>
    </el-dialog>
    <el-dialog :title="$t('order.send_mate')" :visible.sync="detailVisible"  width="760px">
      <el-table :data="mateList" :header-cell-style="{background:'#DFEBF9',color:'#606266'}" max-height="300"
        :empty-text="$t('common.nodata')" :row-class-name="tableRowClassName" border>
        <el-table-column :show-overflow-tooltip='true' prop="part_num" :label="$t('storage.part_num')">
        </el-table-column>
        <el-table-column :show-overflow-tooltip='true' prop="label_code" :label="$t('storage.label_code')">
        </el-table-column>
        <el-table-column :show-overflow-tooltip='true' prop="shelf_id" :label="$t('storage.shelf_id')">
        </el-table-column>
        <el-table-column :show-overflow-tooltip='true' prop="position" :label="$t('storage.position')">
        </el-table-column>
        <el-table-column :show-overflow-tooltip='true' prop="quantity" :label="$t('storage.quantity')">
        </el-table-column>
      </el-table>
    </el-dialog>
  </div>
</template>

<script>
  import {
    orderDetail
  } from '@/request/api/order'
  export default {
    data() {
      return {
        traceVisible: false,
        detailVisible:false,
        traceList: [],
        mateList:[]
      }
    },
    methods: {
      open(id) {
        orderDetail({
          order_id: id
        }).then(res => {
          this.traceVisible = true
          if (res.result) {
            this.traceList = res.data
          } else {
            this.$message({
              showClose: true,
              message: res.msg,
              type: 'error'
            })
          }
        })
      },
      stateFormatter(value){
        if(value.mate_state ==1){
          return '已发'
        }else if(value.mate_state ==2){
          return '完成'
        }else{
          return '未发'
        }
      },
      tableRowClassName({row, rowIndex}){
      	if(row.mate_state==1){
      		return 'nowmate'
      	} else if(row.mate_state==2){
      		return 'lackmate'
      	} else if(row.mate_state==0){
      		return 'beforemate'
      	}
      	return ''
      },
      getDetail(mateList){
        this.mateList = mateList
        this.detailVisible = true
      }
    }
  }
</script>

<style>
  .el-table .beforemate {
    background: rgba(26, 250, 41,0.1);
  }
  .el-table .nowmate {
    background: rgba(255, 165, 0,0.2);
  }
  .el-table .lackmate {
    background: rgba(216, 30, 6,0.1);
  }
</style>



