<template>
  <div>
    <el-dialog :title="$t('order.edit')" :visible.sync="editVisible" @close="reset('orderform')" width="446px">
      <div class="add_order_form">
        <el-form ref="orderform" :inline="true" :model="orderform" :rules="rules" label-width="100px">
          <el-form-item :label="$t('order.quantity')" prop="order_num" class="form_item_left">
            <el-input v-model="orderform.order_num"></el-input>
          </el-form-item><br>
          <div style="text-align:center">
            <el-button @click="userCancel('orderform')">{{$t('common.cancel')}}</el-button>
            <el-button type="primary" @click="orderSave('orderform')">{{$t('common.save')}}</el-button>
          </div>

        </el-form>
      </div>
    </el-dialog>
  </div>
</template>
<script>
  import {
updateOrderQty
  } from '@/request/api/order'; // 导入api接口
  export default {
    data() {
      return {
        editVisible: false,
        orderform: {
          order_id:"",
          order_num: ""
        },
        rules: {
          order_num: [{
              required: true,
              message: this.$t('order.quantityhoulder'),
              trigger: 'blur'
            },
            {
              validator: (rule, value, callback) => {
              if ( /^[0-9]*$/.test(value)==false) {
                callback(new Error(this.$t('order.numreg')));
              }else{
              	callback();
              }
            }, trigger: 'blur'}
          ],
        }
      }
    },
    mounted() {

    },
    methods: {
      open(row) {
        this.orderform.order_id = row.order_id
        this.orderform.order_num = row.order_num
        this.editVisible = true;
      },
      reset(orderform) {
        this.$refs.orderform.resetFields();
      },
      // 保存用户
      orderSave() {
      updateOrderQty({order_id:this.orderform.order_id,new_qty:this.orderform.order_num}).then(res =>{
        if (res.result) {
          this.$emit('listenFresh')
          this.editVisible = false
        } else{
          this.$message({
            showClose:true,
            message:res.msg,
            type:'error'
          })
        }
      })
      },
      // 取消保存
      userCancel(orderform) {
        this.$refs.orderform.resetFields();
        this.editVisible = false;
      }
    }
  }
</script>
<style>
  .add_order_form {
    text-align: left;
  }

  .add_order_form .el-input__inner {
    width: 220px;
  }

  .form_item_left {
    text-align: left !important;
  }

  /*.add_order_form {
	margin:0 20px;
}*/
  .small_form .el-form-item__label {
    text-align: left;
  }

  .small_form .el-form-item__content {
    width: 200px;
  }

  .small_form .el-input .el-input__inner {
    width: 200px;
  }
</style>



