<template>
  <div>
    <v-search @search="getOrderList"></v-search>
    <v-editorder ref="editorder" @listenFresh="getOrderList"></v-editorder>
    <v-analyze ref="analyze"></</v-analyze>
    <v-trace ref="ordertrace"></v-trace>
    <el-table :data="orderList" :header-cell-style="{background:'#DFEBF9',color:'#606266'}"
      :empty-text="$t('common.nodata')" border>
     <!-- <el-table-column type="selection" width="50">
      </el-table-column> -->
      <el-table-column :show-overflow-tooltip='true' prop="order_name" :label="$t('order.order_name')">
      </el-table-column>
      <el-table-column :show-overflow-tooltip='true' prop="order_num" :label="$t('order.quantity')">
      </el-table-column>
      <el-table-column :show-overflow-tooltip='true' prop="file_name" :label="$t('order.file_name')">
      </el-table-column>
      <el-table-column :show-overflow-tooltip='true' prop="show_file_type" :label="$t('order.file_type')">
      </el-table-column>
      <el-table-column :label="$t('common.operation')" width="300px">
        <template slot-scope="scope">
          <el-button type="warning" size="mini" @click="analyze(scope.row)">{{$t('order.analyze')}}</el-button>
          <el-button type="success" size="mini" @click="orderTrace(scope.row.order_id)">{{$t('order.trace')}}</el-button>
          <el-button type="primary" size="mini" @click="editOrder(scope.row)">{{$t('common.edit')}}</el-button>
          <el-button type="danger" size="mini" @click="orderDelete(scope.row.order_id)">{{$t('common.delete')}}</el-button>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>
<script>
  import VSearch from './search'
  import VEditorder from './editorder'
  import VAnalyze from './analyze'
  import VTrace from './ordertrace'
  import {
    orderList,
    delOrder
  } from '@/request/api/order'
  export default {
    components: {
      VSearch,
      VEditorder,
      VAnalyze,
      VTrace
    },
    data() {
      return {
        orderList: [],
        selection: [],
      }
    },
    mounted() {
      this.getOrderList()
    },
    methods: {
      getOrderList(file_type, order_name) {
        orderList({
          file_type: file_type,
          order_name: order_name
        }).then(res => {
          if (res.result) {
            this.orderList = res.data
          } else {
            this.$message({
              showClose: true,
              message: res.msg,
              type: 'error'
            })
          }
        })
      },
      // selectChange(value) {
      //   let arr = []
      //   value.forEach(item => {
      //     arr.push(item.order_id)
      //   })
      //   this.selection = Array.from(new Set(arr))
      //   console.log(this.selection)
      // },
      analyze(row){
        this.$refs.analyze.open(row)
      },
      editOrder(row){
        this.$refs.editorder.open(row)
      },
      orderTrace(id){
        this.$refs.ordertrace.open(id)
      },
      orderDelete(id) {
        this.$confirm(this.$t('order.orderdeletetips'), this.$t('order.tips'), {
          confirmButtonText: this.$t('common.sure'),
          cancelButtonText: this.$t('common.cancel'),
          type: 'warning'
        }).then(() => {
          delOrder({
            order_id: id
          }).then(res => {
            if (res.result) {
              this.$message({
                showClose: true,
                message: this.$t('common.deleteSuccess'),
                type: 'success'
              })
              this.getOrderList()
            } else {
              this.$message({
                showClose: true,
                message: res.msg,
                type: 'error'
              })
            }
          })
        })
      }
    }
  }
</script>

<style>
</style>



