<template>
  <div>
    <el-dialog :title="$t('order.mate_analyze')" :visible.sync="analyzeVisible" @close="reset('orderform')" width="760px">
      <div class="analyzelights">
          <div class="lightscomp">
             <v-lights></v-lights>
           </div>
          <el-button type="primary" size="mini" @click="sendOrder">{{$t('feeder.send')}}</el-button>
      </div>
      <el-table :data="analyzeList" :header-cell-style="{background:'#DFEBF9',color:'#606266'}" max-height="300"
        :empty-text="$t('common.nodata')" border>
        <el-table-column :show-overflow-tooltip='true' prop="master_pn" :label="$t('order.master_pn')">
        </el-table-column>
        <el-table-column :show-overflow-tooltip='true' prop="need_num_count" :label="$t('order.need_num_count')">
        </el-table-column>
        <el-table-column :show-overflow-tooltip='true' prop="total_quantity" :label="$t('order.total_quantity')">
        </el-table-column>
        <!-- <el-table-column :label="$t('common.operation')" width="240px">
          <template slot-scope="scope">
            <el-button type="warning" size="mini" @click="analyze(scope.row.order_id)">{{$t('order.analyze')}}</el-button>
            <el-button type="primary" size="mini" @click="editOrder(scope.row)">{{$t('common.edit')}}</el-button>
            <el-button type="danger" size="mini" @click="orderDelete(scope.row.order_id)">{{$t('common.delete')}}</el-button>
          </template>
        </el-table-column> -->
      </el-table>
    </el-dialog>
  </div>
</template>

<script>
  import { sendMateByOrder } from '@/request/api/order'
  import VLights from '@/components/material/materialcommon/lights'
  export default{
    components:{VLights},
    data() {
      return {
        analyzeVisible:false,
        analyzeList:[],
        row:''
      }
    },
    methods:{
      open(row){
        sendMateByOrder({order_id:row.order_id,op_type:row.file_type,color:3,send_type:0,file_name:row.file_name}).then( res =>{
          if (res.result) {
            this.row = row
            this.analyzeVisible = true
            this.analyzeList = res.mate_analyze
          } else{
            this.$message({
              showClose:true,
              message:res.msg,
              type:"error"
            })
          }
        })
      },
      sendOrder(){
        sendMateByOrder({order_id:this.row.order_id,op_type:this.row.file_type,color:this.$store.state.color,send_type:1,file_name:this.row.file_name}).then( res =>{
          if (res.result) {
            this.analyzeVisible = false
            this.analyzeList = res.mate_analyze
          } else{
            this.$message({
              showClose:true,
              message:res.msg,
              type:"error"
            })
          }
        })
      }
    }
  }
</script>

<style>
  .analyzelights{
    display: flex;
    margin-bottom: 10px;
    line-height: 46px;
    align-items: center;
  }
  .analyzelights .el-button{
    height: 32px;
    width: 80px;
  }
</style>



