<template>
	<div class="order_search">
    <div class="order_item_div">
      <span class="order_item_name">{{$t('order.file_type')}}</span>
      <div class="operation_select input_wrap">
        <el-select value-key="file_type" v-model="file_type" >
          <el-option v-for="item in fileTypeList" :key="item.file_type" :label="item.type_name" :value="item.file_type">
          </el-option>
        </el-select>
      </div>
    </div>
    <div class="order_item_div">
      <span class="order_item_name">{{$t('order.order_name')}}</span>
      <el-input v-model="order_name" clearable></el-input>
    </div>
		<div class="addorder">
      <el-button type="primary" @click="orderSearch" size="mini" >{{$t('common.search')}}</el-button>
      <!-- <el-button type="warning" @click="orderCheck" size="mini" >{{$t('order.checkanalyze')}}</el-button> -->
		</div>

	</div>
</template>
<script>
	export default {
		data () {
			return {
        file_type:'',
        order_name:'',
        fileTypeList:[
          {
            file_type:'',
            type_name:'所有'
          },{
          file_type:1,
          type_name:'站位表'
        },{
          file_type:2,
          type_name:'取料表'
        }]
			}
		},
		methods: {
			orderCheck () {

			},
			orderSearch () {
				this.$emit('search',this.file_type,this.order_name)
			}
		}
	}
</script>
<style>
	.order_search{
		height: 40px;
		line-height: 40px;
		display: flex;
		font-size: 14px;
		margin:10px 0;
	}
	.addorder{
		margin:0 20px;
	}
	.addorder .el-button{
		height: 40px;
	}
  .order_item_div{
    display:flex;
  }
  .order_item_div .el-input__inner{
    width:160px;
  }
  .order_item_div .el-input{
    width:160px;
  }
  .order_item_name{
    display:block;
    padding:0 20px;
  }
</style>



